import React, {Component} from 'react';
import * as axios from 'axios'
import * as d3 from 'd3'

import interceptgraph_build from "./function/interceptgraph";
import slopechart_build from "./function/slopechart";

class Example_1 extends Component {
    constructor(props){
        super(props);
        this.state = {
            item : 'react component'
        }
    }


    componentDidMount(){

        let dataFile = 'example_1.csv'


            axios.get(`staticData/${dataFile}`)
                .then(data=>{

                    interceptgraph_build('interceptGraph_SVGContainer_1', data.data)
                    slopechart_build('#slopeGraph_SVGContainer_1', data.data)

                })

    }

    render(){

        return(
            <div className="charts_container">
                <svg id="slopeGraph_SVGContainer_1" width='500px' height='500px'/>
                <svg id="interceptGraph_SVGContainer_1" width='500px' height='500px'/>
            </div>
        )
    }
}

export default Example_1;
